import { Component, OnInit } from '@angular/core';
import { SettingsService } from '../../../core/application';

@Component({
  selector: 'kt-after-sales-service',
  templateUrl: './after-sales-service.component.html',
  styleUrls: ['./after-sales-service.component.scss']
})
export class AfterSalesServiceComponent implements OnInit {

  constructor(private settingsService: SettingsService) { }

  ngOnInit() {
    this.settingsService.getAll('AfterSaleStatus').subscribe(
      res => {
        localStorage.setItem('AfterSaleStatus', JSON.stringify(res))
      },
      err => console.log(err)
    )
  }

}
