import { Component, OnInit } from '@angular/core';
import {
  Column, GridOption, OnEventArgs, AngularGridInstance,
  GridService, Formatters, Formatter, FieldType, Editors, BsDropDownService
} from 'angular-slickgrid';
import { AfterSaleService,AfterSaleHistory } from '../../../../core/application';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { Router } from '@angular/router';
import Swal from 'sweetalert2';
import { DatePipe } from '@angular/common';
import { NumberToLetter, getRequiredFields } from '../../../../../helpers';

const customStatusFormatter1: Formatter = (row: number, cell: number, value: any, columnDef: Column, dataContext: any, grid: any) => {
  const status = ['test', 'kt-badge--dark', 'kt-badge--warning', 'kt-badge--success','test', 'kt-badge--danger']
  return `<span class="kt-badge ${status[dataContext.old_status_id]} kt-badge--inline kt-badge--pill kt-badge--rounded">${value}</span>`;

};


const customStatusFormatter2: Formatter = (row: number, cell: number, value: any, columnDef: Column, dataContext: any, grid: any) => {
  const status = ['test', 'kt-badge--dark', 'kt-badge--warning', 'kt-badge--success','test', 'kt-badge--danger']
  return `<span class="kt-badge ${status[dataContext.new_status_id]} kt-badge--inline kt-badge--pill kt-badge--rounded">${value}</span>`;

};

const customEnableButtonFormatter: Formatter = (row: number, cell: number, value: any, columnDef: Column, dataContext: any, grid: any) => {
  return `<span>
            <button class="btn btn-xs btn-default">
              <i class="fa ${value ? 'fa-check-circle' : 'fa-circle-thin'} fa-lg" style="color: ${value ? 'black' : 'lavender'}"></i>
            </button>
          </span>`;
};

@Component({
  selector: 'kt-after-sales-history-list',
  templateUrl: './after-sales-history-list.component.html',
  styleUrls: ['./after-sales-history-list.component.scss']
})
export class AfterSalesHistoryListComponent implements OnInit {

  savGrid: AngularGridInstance;
  grid: any;
  gridService: GridService;
  dataView: any;

  columnDefinitions: Column[] = [];
  gridOptions: GridOption = {};
  dataset: any = [];

  selected: any;

  constructor(private afterSaleService: AfterSaleService,
    private router: Router,
    private datePipe: DatePipe,
    private modalService: NgbModal,
    private bsDropdown: BsDropDownService) { }

  savGridReady(angularGrid: AngularGridInstance) {
    this.savGrid = angularGrid;
    this.dataView = angularGrid.dataView;
    this.grid = angularGrid.slickGrid;
    this.gridService = angularGrid.gridService;
  }

  ngOnInit() {
    this.columnDefinitions = [
      {
        id: 'id', name: 'Id', field: 'id', sortable: true, width: 30,
        formatter: Formatters.editIcon,
      },
      { id: 'patient_name', name: 'Nom du patient', field: 'after_sale.patient.full_name', sortable: true, width: 120, filterable: true },
      { id: 'product_code', name: 'Code Produit', field: 'after_sale.product.product_code', sortable: true, filterable: true },
      { id: 'product_description', name: 'Description Produit', field: 'after_sale.product.description', sortable: true, filterable: true },
      { id: 'serial_number', name: 'Numero de serie', field: 'serial_number', width: 70, filterable: true },
      {
        id: 'is_warranty',
        name: 'Garantie ',
        field: 'after_sale.is_warranty',
        filterable: true,
        formatter: customEnableButtonFormatter,
      },
      {
        id: 'warranty_end_date', name: 'Date fin garranty ', field: 'warranty_end_date',
        sortable: true,
        resizable: true,
        type: FieldType.string,
        editor: {
          model: Editors.text
        },
      },
      { id: 'status', name: 'Status 1', field: 'old_status.description', formatter: customStatusFormatter1, width: 140, filterable: true },
      { id: 'status2', name: 'Status 2', field: 'new_status.description', formatter: customStatusFormatter2, width: 140, filterable: true },

    ];
    this.gridOptions = {
      enableAutoResize: true,       // true by default
      enableCellNavigation: true,
      enableFiltering: true,
      enablePagination: true,
      enableColumnPicker: true,
      enableExcelCopyBuffer: true,
      enableAsyncPostRender: true, // for the Angular PostRenderer, don't forget to enable it
      asyncPostRenderDelay: 0,    // also make sure to remove any delay to render it
      dataItemColumnValueExtractor: function getItemColumnValue(item, column) {
        var val = undefined;
        try {
          val = eval("item." + column.field);
        } catch (e) {
          // ignore
        }
        return val;
      }
    };

    // fill the dataset with your data

    this.afterSaleService.getAllAfterSaleHistory().subscribe(
      res => this.dataset = res,
      err => console.log('errro'),
      () => console.log('finish')
    )
  }

}
